.footer-container {
    background: radial-gradient(
      circle,
      rgba(2, 0, 36, 1) 0%,
      rgb(16, 58, 52) 50%,
      rgba(236, 246, 47, 0.938) 100%
    );
    color: white;
    margin-top: 50px;
  }
  
  .footer-wrap {
    padding: 50px 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 2500px;
    margin: 0 auto;
  }
  
  .social-media {
    max-width: 1300px;
    width: 100%;
  }
  
  .social-media-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1500px;
    margin: 16px auto 0 auto;
  }
  
  .social-logo {
    justify-self: start;
    cursor: pointer;
    text-decoration: none;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    margin-bottom: 16px;
    font-weight: bold;
  }
  
  @media screen and (max-width: 820px) {
    flex-direction: column;
  }
  
  .social-icons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 240px;
    cursor: pointer;
    color: white;
  }
  .social-icons-link:hover {
    border-bottom: 1px solid #fff;
    -webkit-transform: scale(1.3);
    -ms-transform: scale(1.3);
    transform: scale(1.3);
    transition: 0.3s;
  }
  
  .social-icons > .social-icons-link {
    color: #fff;
    font-size: 24px;
    transform: 0.1s ease-in-out;
    color: white;
  }
  
  .a-link {
    text-decoration: none;
    color: white;
  }
  